body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.main {
	width: 500px;
}

.title {
	width: 500px;
	text-align: center;
	font-size: x-large;
	font-weight: 600;
}

.sample {
	width: 500px;
  margin: 10px;
  padding: 20px;
	border: 1px solid black;
}

.descr {
	width: 500px;
  margin: 10px;

}



.calender-input-field {
  display: inline-block;
}

.arrow {
  display: -webkit-inline-flex;
  display: inline-flex;
	font-size: 0; /* remove space between input and button div */
	width: 15px;
	height: 15px;
	margin: 2px;
}

.img_cal {
	fill: navy;
	width: 21px;
	height: 21px;
}

.selected-date {
	color: black;
	background-color: #B0C4DE;
}

.input-field {
    display: inline-block;
    width: 140px;
}

.input-field-box {
  display: inline-block;
	height: 21px;
  width: 140px;
}

/* Popup container */
.field-component {
  display: -webkit-inline-flex;
  display: inline-flex;
	font-size: 0; /* remove space between input and button div */
}

.button-open {
  display: inline-block;
  cursor: pointer;
	background-color: navy;
	height: 21px;
	width: 21px;
}

.calender {
	position: absolute;
	width: 160px;
	z-index: 10;
	background-color: white;
	border: solid 1px black;
}

.calender-show {
	visibility: visible;
}

.control {
  display: -webkit-inline-flex;
  display: inline-flex;
	width: 160px;
}

.button-next {
  display: inline-block;
  cursor: pointer;
}

.button-prev {
  display: inline-block;
  cursor: pointer;
}

.main {
	width: 160px;
}

.header {
	width: 160px;
}

.hcell {
  display: inline-block;
	width: 20px;
	text-align: center;
	padding: 1px;
}

.row {
}

.rcell {
  display: inline-block;
	width: 20px;
	text-align: center;
	padding: 1px;
  cursor: pointer;
}

.rcell:hover {
	background-color: gray;
}

.off {
	color: red;
}

.not-this-month {
	opacity: 0.3;
}

.popup {
  position: relative;
  display: inline-block;
  cursor: pointer;
}



